.custom-file-upload {
    display: inline-block;
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .custom-file-upload:hover {
    background-color: #45a049;
  }
  
  .custom-file-upload:active {
    background-color: #3e8e41;
  }
  #imgPrv{
    width: 700px;
  }
  @media (max-width: 1100px){
    #imgPrv{
        width: 300px;
      }
  }